import {Fonts, memoForwardRef} from '@true/core';
import {memo, RefObject, useCallback} from 'react';
import {styled} from '@true/core';
import {
  navigateToCreateEvent,
  navigateToCreatePollScreen,
  navigateToCreatePostScreen,
  navigateToCreateStyledUpdateScreen,
  navigateToCreateTopicScreen,
} from '@/utils';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
interface TypePostThumbnailProps {
  icon: any;
  text: string;
  type: string;
}

export const TypePostThumbnail = memo(function (props: TypePostThumbnailProps) {
  const {icon, text, type} = props;
  const _onPress = useCallback(() => {
    if (
      type === 'update' ||
      type === 'welcome' ||
      type === 'idea' ||
      type === 'announcement'
    ) {
      navigateToCreatePostScreen({
        type: type,
      });
    }
    if (type === 'styled') {
      navigateToCreateStyledUpdateScreen({});
    }
    if (type === 'events') {
      navigateToCreateEvent({
        id: -1,
      });
    }
    if (type === 'polls') {
      navigateToCreatePollScreen({
        id: -1,
      });
    }
    if (type === 'topics') {
      navigateToCreateTopicScreen({
        id: -1,
      });
    }
    return;
  }, [type]);
  return (
    <SContainer onPress={_onPress}>
      <SIcon>
        <SImage source={icon} />
      </SIcon>
      <STextView>
        <STextType>{text}</STextType>
      </STextView>
    </SContainer>
  );
});
const SContainer = styled.TouchableOpacity`
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 16px 0 16px;
`;
const SIcon = styled.View`
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 24px;
  height: 24px;
`;
const STextView = styled.View`
  flex: 1;
  padding-bottom: 16px;
  border-bottom-width: 1px;
  border-bottom-color: #ebebeb;
  margin-left: 8px;
`;
const STextType = styled.Text`
  font-family: ${Fonts.Regular};

  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242424;
`;
